@media screen and (min-width: 320px) and (max-width: 767px) {
  * {
    box-sizing: border-box;
    padding: 0px;
    margin: 0px;
  }

  body {
    min-width: 320px;
    max-width: 767px;
    position: relative;
    color: getColor('text');
    font-family: 'DM Sans', 'Titan One', sans-serif;
    margin: auto;
  }
  .container {
    margin: auto;
    align-items: center;
    padding: 0px 20px;
  }
}

@media screen and (min-width: 768px) and (max-width: 1280px) {
  body {
    min-width: 768px;
    max-width: 1279px;
  }
  .container {
    padding: 0px 36px;
  }
}

@media screen and (min-width: 1280px) {
  body {
    max-width: 1280px;
  }
  .container {
    padding: 0px 115px;
  }
}
